input {
  transition: all 300ms cubic-bezier(0.175, 0.885, 0.335, 1);
}

.search_modal {
  position: absolute;
  width: 700px;
  height: 500px;
  margin-top: 152px;
  background-color: #4D5764;
  z-index: 101;
  top: 50px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.games-label {
  font-size: 14px;
  padding-left: 50px;
  padding-top: 10px;
  padding-right: 50px;
  background-color: #4D5764;
}

.search_modal__container {
  margin-top: 60px;
  height: 10px;
}

.search_results_container {
  display: flex;
  flex-direction: column;
}

.game-listing-size {
  height: 91.5px;
  background-color: #4D5764;
}

.game-listing {
  border: none;
  width: auto;
  padding: 12px 0;
  background-color: #4D5764;
}

#search-card-title {
  background-color: #4D5764;
  font-weight: normal;
  margin-bottom: 5px;
}

.game-listing-information {
 margin: 0;
 background-color: #4D5764;

}

.game-listing-information-header {
  padding: 0;
  margin: 0;
 background-color: #4D5764;
}

.spacer-div {
 border: none;
 padding: 0;
 margin: 0;
}

.game-listing-information-body {
  padding: 0;
  margin: 0;
 background-color: #4D5764;
}

#no-search-results {
  margin-top: 25px;
  margin-left: 40px;
  text-align: left;
  background-color: #4D5764;
}

.game-listing-information-header > img {
  border-radius: 4px;
  height: 120px;
  width: 240px;
  object-fit: contain;
  background-color: #4D5764;
  padding-right: 115px;
  border-radius: 0;
  margin: 0;
}
